body {
    background: white !important; }

header {
    height: auto;
    padding-top: 20px;
    padding-bottom: 0px; }

.header {

    &--media {
        height: 80%;
        background: rgba(0,0,0,.3);

        @media (max-width: 340px) {
            height: 90%; } } }

.page {

    &--dark {
        background: black !important;

        & body, & main {
            background: black !important; } } }

// Page specific modules (in the interest of time, i put the code here)
.team {

    & .section__inner {

        @media (max-width: 1390px) {
            flex-flow: column;
            flex-wrap: wrap; } }

    & .g33 {

        &:first-child {

            @media (max-width: 1390px) {
                padding-left: 150px; }

            @media (max-width: 1210px) {
                padding-left: 100px; }

            @media (max-width: 1074px) {
                padding-left: 0; } }

        &:nth-child(2) {

            @media (max-width: 1390px) {
                display: flex;
                justify-content: flex-end;
                padding-right: 150px;
                margin-top: -100px; }

            @media (max-width: 914px) {
                margin-top: -30px; } }

        &:last-child {

            @media (max-width: 1390px) {
                padding-left: 150px;
                margin-top: -100px; }

            @media (max-width: 1210px) {
                padding-left: 100px; }

            @media (max-width: 1074px) {
                padding-left: 0; }

            @media (max-width: 914px) {
                margin-top: -30px; } }

        @media (max-width: 790px) {
            display: flex !important;
            justify-content: center !important;
            padding: 0 !important;
            margin-top: 25px !important; }

        @media (max-width: 600px) {
            align-self: flex-start; }

        & .persona {

            @media (max-width: 790px) {
                margin-left: -100px; }

            @media (max-width: 600px) {
                margin-left: 0px; } } }

    & .g50 {

        &:first-child {

            @media (max-width: 1390px) {
                padding-left: 150px; }

            @media (max-width: 1210px) {
                padding-left: 100px; }

            @media (max-width: 1074px) {
                padding-left: 0; } }

        &:last-child {

            @media(min-width: 1391px) {
                padding-left: 44%; }

            @media (max-width: 1390px) {
                padding-left: 150px; }

            @media (max-width: 1210px) {
                padding-left: 100px; }

            @media (max-width: 1074px) {
                padding-left: 0; } }

        @media (max-width: 1386px) {
            display: flex !important;
            justify-content: center !important;
            padding: 0 !important;
            margin-top: 25px !important; }

        @media (max-width: 600px) {
            align-self: flex-start; }

        & .persona {

            @media (max-width: 790px) {
                margin-left: -100px; }

            @media (max-width: 600px) {
                margin-left: 0px; } } } }
.values {

    & h3 {

        @media (max-width: 880px) {
            font-size: 35px;
            line-height: 40px; } }

    &__text {
        min-height: 295px;

        @media (max-width: 1200px) {
            min-height: 340px; }

        @media (max-width: 700px) {
            min-height: 0; } } }

.social {

    @media (max-width: 1188px) {
        flex-flow: column;
        align-items: center; }

    &__text {

        @media (max-width: 1188px) {
            margin-bottom: 0;
            padding-right: 0; }

        & div {

            @media (max-width: 1188px) {
                text-align: center;
                align-items: center; } } }

    &__networks {

        @media (max-width: 650px) {
            transform: scale(.8); }

        @media (max-width: 425px) {
            transform: scale(.6);
            margin-top: 0; } } }
